export default {
	data() {
		return {
			picList: [],
			indicatorDots: false,
			autoplay: false,
			duration: 500,
			circular: true,
			current: 0,
			isShowSwiper: false
		}
	},
	onLoad() {
		this.picListInit(); 
	},
	methods: {
		clickPic(index) {
			this.current = index;
			this.isShowSwiper = true;
		},
		async picListInit() {
			this.current = uni.getStorageSync("currentImgIndex");
			this.picList = uni.getStorageSync("imgPreviewPicList");
			console.log(this.current + '222' + this.picList)
		},
		changeSwiper(e) {
			this.current = e.target.current;
		}
	}
}
